import React from 'react'
import ScrollToTop from 'react-scroll-up'
import { Button } from 'reactstrap'
import * as Icon from 'react-feather'
import classnames from 'classnames'
import useDeviceDetect from '../../../customHooks/useDeviceDetect'
import { FormattedMessage } from 'react-intl'
import { IMAGE } from '../../../configs/app-configs'

const Footer = (props) => {
  const { isMobile } = useDeviceDetect()
  return (
    <footer>
      <div
        className={classnames('footer footer-light', {
          'd-none': isMobile
        })}
      >
        <div className='d-flex justify-content-between'>
          <div className='float-md-left d-block d-md-inline-block mt-25'>
            <div>
              <FormattedMessage id='footer.copyRight' />
            </div>
            <div>
              <FormattedMessage id='footer.companySlogan' />
            </div>
          </div>
          <div className='float-md-right d-none d-md-block'>
            <img
              src={IMAGE.DOWNLOAD_APP}
              alt='DOWNLOAD ON APP STORE'
            />
          </div>
        </div>
      </div>
      <div
        className={classnames('footer footer-light footer-mobile', {
          'd-none': !isMobile
        })}
      >
        <div>
          <a className='tab-link' href='#'>
            <Icon.Home />
          </a>
        </div>
        <div>
          <a className='tab-link' href='#'>
            <Icon.List />
          </a>
        </div>
        <div>
          <a className='tab-link' href='#'>
            <Icon.PlusCircle />
          </a>
        </div>
        <div>
          <a className='tab-link' href='#'>
            <Icon.Gift />
          </a>
        </div>
        <div>
          <a className='tab-link'>
            <Icon.MessageSquare />
          </a>
        </div>
      </div>
      {props.hideScrollToTop === false ? (
        <ScrollToTop showUnder={160}>
          <Button color='primary' className='btn-icon scroll-top'>
            <Icon.ArrowUp size={15} />
          </Button>
        </ScrollToTop>
      ) : null}
    </footer>
  )
}

export default Footer
