import React, { useState } from 'react'
import { Check } from 'react-feather'
import { FormattedMessage } from 'react-intl'
import { FormGroup, Label, Button } from 'reactstrap'
import '../../assets/scss/pages/authentication.scss'
import CheckBox from '../../components/Checkbox'
import { Formik, Field, Form } from 'formik'
import * as Yup from 'yup'

const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/

const formSchema = Yup.object().shape({
  fullName: Yup.string().required(
    <FormattedMessage id='register.fullname.required' />
  ),
  email: Yup.string()
    .required(<FormattedMessage id='register.email.required' />)
    .email(<FormattedMessage id='register.email.invalid' />),
  phoneNumber: Yup.string()
    .matches(phoneRegExp, () => (
      <FormattedMessage id='register.phoneNumber.invalid' />
    ))
    .required(<FormattedMessage id='register.phoneNumber.required' />)
})

const Register = () => {
  const [isAppcepted, setIsAppcepted] = useState(false)
  const [isNotApccepted, setIsNotAccepted] = useState(false)

  const onSubmit = (values, actions) => {
    setTimeout(() => {
      if (!isAppcepted) {
        setIsNotAccepted(true)
        return
      }
      alert(JSON.stringify(values, null, 2))
      actions.setSubmitting(false)
    }, 1000)
  }

  const ontoggleAccepted = (checked) => {
    setIsAppcepted(checked)
    setIsNotAccepted(!checked)
  }

  return (
    <Formik
      initialValues={{
        fullName: '',
        email: '',
        phoneNumber: '',
        referalCode: ''
      }}
      onSubmit={onSubmit}
      validationSchema={formSchema}
    >
      {({ errors, touched }) => (
        <Form>
          <FormGroup className='form-label-group position-relative'>
            <FormattedMessage id='register.fullname'>
              {(msg) => (
                <React.Fragment>
                  <Field
                    name='fullName'
                    className={`form-control ${
                      errors.fullName && touched.fullName && 'is-invalid'
                    }`}
                    placeholder={msg}
                  />
                  {errors.fullName && touched.fullName ? (
                    <div className='text-danger'>{errors.fullName}</div>
                  ) : null}
                  <Label>{msg}</Label>
                </React.Fragment>
              )}
            </FormattedMessage>
          </FormGroup>
          <FormGroup className='form-label-group position-relative'>
            <Field
              name='email'
              className={`form-control ${
                errors.email && touched.email && 'is-invalid'
              }`}
              placeholder='Email *'
            />
            {errors.email && touched.email ? (
              <div className='text-danger'>{errors.email}</div>
            ) : null}
            <Label>Email *</Label>
          </FormGroup>
          <FormGroup className='form-label-group position-relative'>
            <FormattedMessage id='register.phoneNumber'>
              {(msg) => (
                <React.Fragment>
                  <Field
                    name='phoneNumber'
                    className={`form-control ${
                      errors.phoneNumber && touched.phoneNumber && 'is-invalid'
                    }`}
                    placeholder={msg}
                  />
                  {errors.phoneNumber && touched.phoneNumber ? (
                    <div className='text-danger'>{errors.phoneNumber}</div>
                  ) : null}
                  <Label>{msg}</Label>
                </React.Fragment>
              )}
            </FormattedMessage>
          </FormGroup>
          <FormGroup className='form-label-group position-relative'>
            <FormattedMessage id='register.referalCode'>
              {(msg) => (
                <React.Fragment>
                  <Field
                    name='referalCode'
                    className='form-control'
                    placeholder={msg}
                  />
                  <Label>{msg}</Label>
                </React.Fragment>
              )}
            </FormattedMessage>
          </FormGroup>
          <FormGroup>
            <div className='d-flex align-items-center'>
              <CheckBox
                color='primary'
                icon={<Check className='vx-icon' size={16} />}
                onChange={(e) => ontoggleAccepted(e.target.checked)}
                defaultChecked={isAppcepted}
              />
              <div>
                Ti ng  vi{' '}
                <a className='text-primary'>iu khon v iu kin</a> s dng
                dch v.
              </div>
            </div>
            {isNotApccepted ? (
              <div className='text-danger'>
                <FormattedMessage id='register.mustAppcepted' />
              </div>
            ) : null}
            <div className='d-flex justify-content-center mt-2'>
              <Button color='primary' type='submit'>
                <FormattedMessage id='register' />
              </Button>
            </div>
          </FormGroup>
        </Form>
      )}
    </Formik>
  )
}

export default Register
