import React, { useEffect, useState } from 'react'
import { FormattedMessage } from 'react-intl'
import classnames from 'classnames'
import '../../assets/scss/pages/authentication.scss'
import { IMAGE } from '../../configs/app-configs'
import Login from './Login'
import { useHistory } from 'react-router-dom'
import Register from './Register'
import { IntlContext } from '../../utility/context/Internationalization'
import ForgotPassword from './ForgotPassword'

const LandingPage = (props) => {
  const [activeTab, setActiveTab] = useState('')
  const history = useHistory()

  useEffect(() => {
    setActiveTab(props.activeTab || 'login')
  }, [props.activeTab])

  const TabView = () => {
    switch (activeTab) {
      case 'login':
        return <Login />
      case 'register':
        return <Register />
      case 'forgot-password':
        return <ForgotPassword />
      default:
        return ''
    }
  }

  const goToLink = (link) => history.push(link)

  return (
    <React.Fragment>
      <div
        className='landing-page'
        style={{
          background: `url('${IMAGE.LANDING_PAGE_BG}')`
        }}
      >
        <div className='ld-main ml-auto col-12 col-md-6 col-lg-4'>
          <div className='ld-header d-flex justify-content-between mb-5'>
            <IntlContext.Consumer>
              {(context) => {
                return (
                  <React.Fragment>
                    <img src={IMAGE.LOGO_WHITE} alt='logo' />
                    <div className='languages d-flex align-items-center'>
                      <div
                        onClick={() => context.switchLanguage('vi')}
                        className={classnames('mr-1 cursor-pointer', {
                          'text-primary': context.state.locale === 'vi'
                        })}
                      >
                        VIE
                      </div>
                      <div
                        className='divider mr-1'
                        style={{ height: '15px' }}
                      ></div>
                      <div
                        onClick={() => context.switchLanguage('en')}
                        className={classnames('mr-1 cursor-pointer', {
                          'text-primary': context.state.locale === 'en'
                        })}
                      >
                        ENG
                      </div>
                    </div>
                  </React.Fragment>
                )
              }}
            </IntlContext.Consumer>
          </div>
          <div className='lg-content-header d-flex cursor-pointer'>
            <div
              onClick={() => goToLink('/login')}
              className={classnames('col-6 text-center tab-control', {
                active: activeTab === 'login'
              })}
            >
              <FormattedMessage id='login' />
            </div>
            <div
              onClick={() => goToLink('/register')}
              className={classnames('col-6 text-center tab-control', {
                active: activeTab === 'register'
              })}
            >
              <FormattedMessage id='register' />
            </div>
          </div>
          <div className='lg-content p-2 p-md-4 p-lg-5'>
            <TabView />
          </div>
        </div>
        <div className='ld-footer px-5'>
          <div className='d-flex justify-content-between'>
            <div className='float-md-left d-block d-md-inline-block mt-25'>
              <div>
                <FormattedMessage id='footer.copyRight' />
              </div>
              <div>
                <FormattedMessage id='footer.companySlogan' />
              </div>
            </div>
            <div className='float-md-right d-none d-md-block'>
              <img src={IMAGE.DOWNLOAD_APP} alt='DOWNLOAD ON APP STORE' />
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  )
}

export default LandingPage
