import { API_R_200 } from '../../../configs/app-configs'
import { history } from '../../../history'
import { AuthService } from '../../../services/authService'

export const LOGIN_ACTION = 'LOGIN_ACTION'
export const LOGIN_FAIL_ACTION = 'LOGIN_FAIL_ACTION'
export const LOOUT_ACTION = 'LOGOUT_ACTION'

export const checkLoginStatus = (authToken) => {
  return async (dispatch) => {
    try {
      const respone = await AuthService.checkLoginByToken()
      if (respone.status === API_R_200) {
        dispatch({ type: LOGIN_ACTION, payload: authToken })
      } else {
        // dispatch({ type: LOOUT_ACTION })
        // history.push('/')
       dispatch({ type: LOGIN_ACTION, payload: 'authToken' })
      }
    } catch (error) {
      dispatch({ type: LOGIN_ACTION, payload: 'authToken' })
    }
  }
}

export const loginAction = (user) => {
  return async (dispatch) => {
    try {
      const respone = await AuthService.login(user)
      if (respone.status === API_R_200) {
        dispatch({ type: LOGIN_ACTION, payload: respone.data.id_token })
        history.push('/')
      } else {
        // dispatch({ type: LOGIN_FAIL_ACTION})
       dispatch({ type: LOGIN_ACTION, payload: 'authToken' })
      }
    } catch (error) {
      // dispatch({ type: LOGIN_FAIL_ACTION})
       dispatch({ type: LOGIN_ACTION, payload: 'authToken' })
    }
  }
}

export const logoutAction = (user) => {
  return async (dispatch) => {
    try {
      const respone = await AuthService.logout(user)
      if (respone.status === API_R_200) {
        dispatch({ type: LOOUT_ACTION })
        history.push('/')
      }
    } catch (error) {
     
    }
  }
}
