import React, { useState } from 'react'
import { FormattedMessage } from 'react-intl'
import { FormGroup, Label, Button, Input } from 'reactstrap'
import '../../assets/scss/pages/authentication.scss'
import { Formik, Field, Form, FastField } from 'formik'
import * as Yup from 'yup'
import { Sun } from 'react-feather'

const formSchema = Yup.object().shape({
  username: Yup.string().required(
    <FormattedMessage id='forgotPassword.username.required' />
  ),
  email: Yup.string()
    .required(<FormattedMessage id='forgotPassword.email.required' />)
    .email(<FormattedMessage id='register.email.invalid' />)
})
const ForgotPassword = () => {
  const onSubmit = (values, actions) => {
    actions.setSubmitting(false)
  }

  return (
    <Formik
      initialValues={{
        username: '',
        email: ''
      }}
      onSubmit={onSubmit}
      validationSchema={formSchema}
    >
      {({ errors, touched }) => (
        <Form>
          <h4 className='text-center text-white'>
            <FormattedMessage id='forgotPassword' />
          </h4>
          <FormGroup className='form-label-group position-relative mt-3'>
            <FormattedMessage id='forgotPassword.username'>
              {(msg) => (
                <React.Fragment>
                  <Field
                    name='username'
                    className={`form-control ${
                      errors.username && touched.username && 'is-invalid'
                    }`}
                    placeholder={msg}
                  />
                  {errors.username && touched.username ? (
                    <div className='text-danger'>{errors.username}</div>
                  ) : null}
                  <Label>{msg}</Label>
                </React.Fragment>
              )}
            </FormattedMessage>
          </FormGroup>
          <FormGroup className='form-label-group position-relative'>
            <FormattedMessage id='forgotPassword.email'>
              {(msg) => (
                <React.Fragment>
                  <FastField name='email'>
                    {({ field, form }) => (
                      <Input
                        className={`${
                          errors.email && touched.email && 'is-invalid not-show-icon'
                        }`}
                        placeholder={msg}
                        {...field}
                        onChange={(e) =>
                          form.setFieldValue('email', e.target.value)
                        }
                      />
                    )}
                  </FastField>
                  {errors.email && touched.email ? (
                    <div className='text-danger'>{errors.email}</div>
                  ) : null}
                  <div className='form-control-position'>
                    <Sun size={15} />
                  </div>
                  <Label>{msg}</Label>
                </React.Fragment>
              )}
            </FormattedMessage>
          </FormGroup>
          <div className='d-flex justify-content-center'>
            <Button color='primary' type='submit'>
              <FormattedMessage id='forgotPassword.verify' />
            </Button>
          </div>
        </Form>
      )}
    </Formik>
  )
}

export default ForgotPassword
