import { LOGIN_STATUS } from '../../../configs/app-configs'
import HttpClient from '../../../utility/HttpClient'
import { LOGIN_ACTION, LOGIN_FAIL_ACTION, LOOUT_ACTION } from '../../actions/auth'

const authInitialState = {
  authToken: '',
  user : '',
  loginStatus : ''
}

export const authReducers = (state = { ...authInitialState }, action) => {
  switch (action.type) {
    case LOGIN_ACTION: {
      HttpClient.addAuthTokenToHeader(action.payload)
      return { ...state, authToken: action.payload, loginStatus : LOGIN_STATUS.SUCCESS }
    }
    case LOOUT_ACTION: {
      HttpClient.removeAuthTokenFromHeader('Authorization')
      return { ...authInitialState }
    }
    case LOGIN_FAIL_ACTION: {
      return {...state, loginStatus : LOGIN_STATUS.FAIL}
    }
    default:
      return state
  }
}

export default authReducers
