import React, { useState } from 'react'
import { Check } from 'react-feather'
import { FormattedMessage } from 'react-intl'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { FormGroup, Label, Button, Input } from 'reactstrap'
import '../../assets/scss/pages/authentication.scss'
import CheckBox from '../../components/Checkbox'
import { loginAction } from '../../redux/actions/auth/index'
import { Formik, Field, Form, FastField } from 'formik'
import * as Yup from 'yup'
import { LOGIN_STATUS } from '../../configs/app-configs'

const formSchema = Yup.object().shape({
  username: Yup.string().required(
    <FormattedMessage id='login.username.required' />
  ),
  password: Yup.string().required(
    <FormattedMessage id='login.password.required' />
  )
})
const Login = () => {
  const [rememberMe, setRememberMe] = useState(false)
  const dispatch = useDispatch()

  const loginStatus = useSelector((state) => state.auth.loginStatus)

  const onSubmit = (values, actions) => {
    dispatch(
      loginAction({
        username: values.username,
        password: values.password,
        rememberMe
      })
    )
    actions.setSubmitting(false)
  }

  return (
    <Formik
      initialValues={{
        fullName: '',
        email: '',
        phoneNumber: '',
        referalCode: ''
      }}
      onSubmit={onSubmit}
      validationSchema={formSchema}
    >
      {({ errors, touched }) => (
        <Form>
          <h4 className='text-center text-white'>
            <FormattedMessage id='login.firstWelcome' />
            {loginStatus ===  LOGIN_STATUS.FAIL ? (
              <div className='text-danger mt-1'>
                <FormattedMessage id='login.fail' />
              </div>
            ) : (
              ''
            )}
          </h4>
          <FormGroup className='form-label-group position-relative mt-3'>
            <FormattedMessage id='login.username'>
              {(msg) => (
                <React.Fragment>
                  <Field
                    name='username'
                    className={`form-control ${
                      errors.username && touched.username && 'is-invalid'
                    }`}
                    placeholder={msg}
                  />
                  {errors.username && touched.username ? (
                    <div className='text-danger'>{errors.username}</div>
                  ) : null}
                  <Label>{msg}</Label>
                </React.Fragment>
              )}
            </FormattedMessage>
          </FormGroup>
          <FormGroup className='form-label-group position-relative'>
            <FormattedMessage id='login.password'>
              {(msg) => (
                <React.Fragment>
                  <FastField name='password'>
                    {({ field, form }) => (
                      <Input
                        type='password'
                        className={`form-control ${
                          errors.password && touched.password && 'is-invalid'
                        }`}
                        placeholder={msg}
                        {...field}
                        onChange={(e) =>
                          form.setFieldValue('password', e.target.value)
                        }
                      />
                    )}
                  </FastField>
                  {errors.password && touched.password ? (
                    <div className='text-danger'>{errors.password}</div>
                  ) : null}
                  <Label>{msg}</Label>
                </React.Fragment>
              )}
            </FormattedMessage>
          </FormGroup>
          <FormGroup className='d-flex justify-content-between align-items-center'>
            <CheckBox
              color='primary'
              icon={<Check className='vx-icon' size={16} />}
              label={<FormattedMessage id='login.rememberMe' />}
              onChange={(e) => setRememberMe(e.target.checked)}
              defaultChecked={rememberMe}
            />
            <div className='divider' style={{ height: '30px' }}></div>
            <div className='float-right'>
              <Link to='/forgot-password' className='text-white'>
                <FormattedMessage id='forgotPassword' />
              </Link>
            </div>
          </FormGroup>
          <div className='d-flex justify-content-center'>
            <Button color='primary' type='submit'>
              <FormattedMessage id='login' />
            </Button>
          </div>
        </Form>
      )}
    </Formik>
  )
}

export default Login
